* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

/* ============================== */

img {
  height: 30px;
  width: auto;
}

li {
  list-style: none;
}

.country-list {
  display: block;
  padding-left: 5px;
}

.country-list-item {
  display: flex;
  align-items: center;
  flex-wrap: wrap
}

.country-info_title {
  display: flex;
  align-items: center
}

h1,
h2 {
  padding-left: 30px;
}